@font-face {
	font-family: "SpecialElite";
	src: local("SpecialElite"),
		url("./font/SpecialElite-Regular.ttf") format("truetype");
	font-weight: normal;
}

html {
	font-family: 'Lekton', Helvetica, Arial, sans-serif;
	margin: 0;
	padding: 0;
}

body {
	box-sizing: border-box;
	height: 100vh;
	background: url('./img/face-text.png') top center repeat-x fixed;
	background-size: auto 100vh;
}

* {
	user-select: none;
	-webkit-tap-highlight-color: transparent;
}

h1,
h2 {
	text-align: center;
	margin-top: 0;
	/* background: radial-gradient(white, white 30%, transparent); */
}

.app {
	height: 100%;
	min-height: 100vh;
	background-color: transparent;
}

.app.no-face {
	background-color: white;
	position: relative;
}

.home {
	display: none;
}

.home.show {
	display: flex;
	flex-direction: column;
	width: 100%;
}

.back-button {
	position: absolute;
	top: 0;
	left: 0;
	width: 30px;
	height: 30px;
	background-color: white;
	border: 1px solid #000;
	border-radius: 50%;
	display: flex;
	justify-content: center;
	align-items: center;
}

.back-button svg {
	height: 50%;
	width: 50%;
	margin-right: 5%;
}

.back-button svg g path {
	fill: black;
	height: 10px;
	width: 50%;
}

.gh_link {
	position: fixed;
	top: 5px;
	right: 5px;
	align-items: center;
	justify-content: center;
}

.gh_link:focus {
	outline: none;
}

.link_to_home {
	position: sticky;
	top: 20px;
	left: 20px;
}

.container {
	background: linear-gradient(rgba(255, 255, 255, 0.88) 95%, transparent);
	margin: 0;
	padding-top: 30px;
	padding-bottom: 10vh;
	width: 100%;
	display: flex;
	justify-content: center;
}

.width-fixer {
	max-width: 1350px;
}

.head_area {
	margin-top: 0;
	padding-top: 0;
	background: rgba(253, 252, 252, 0);
}

.intro {
	display: block;
	max-width: 700px;
	/* background: white; */
	margin: 0 auto;
	padding: 0 20px;
}

.card_space {
	display: flex;
	flex-wrap: wrap;
	align-items: flex-start;
	justify-content: center;
	opacity: 1;
	padding-bottom: 30px;
}
.card_space h2 {
	width: 100%;
}

.card {
	width: 300px;
	height: auto;
	min-height: 300px;
	background: hsl(0, 0%, 83%);
	margin: 10px;
	padding: 0 10px;
	padding-top: 0;
	border: solid 1px hsl(0, 0%, 83%);
	box-shadow: hsl(0, 0%, 72%) 2px 2px 3px;
	position: relative;
}

a {
	color: black;
	text-decoration: none;
}

.card_title {
	transition-duration: 200ms;
	text-align: start;
	color: #222;
}

.card_title:hover {
	color: red;
}

.card_origin_badge {
	position: absolute;
	top: 0;
	right: 10px;
	transform: rotate(10deg) translateY(100%);
	font-family: "SpecialElite";
	font-weight: bold;
	color: red;
	text-decoration: underline;
	text-transform: capitalize;
}

.card_image_link {
	margin: 0;
	padding: 0;
}

.card_image_box {
	display: flex;
	justify-content: center;
	align-items: flex-start;
	width: 100%;
	height: 200px;
	overflow: hidden;
	margin: 0;
	padding: 0;
}

.card_image {
	height: 100%;
	margin: 2px;
	transition-duration: 200ms;
}

.card_image:hover {
	transform: scale(1.02);
}

.card_caption {
	margin-top: 15px;
}

.face_spacer {
	height: 100vh;
}
